<template>
    <div class="min-h-[50vh]">





        <div>
            <nav class="bg-gray-800  shadow py-4 ">
                <div class="px-8 mx-auto max-w-7xl">
                    <div class="flex items-center justify-between h-16">
                        <div class=" flex items-center">
                            <a class="flex-shrink-0" href="/Rejeitados">
                                <img class="w-14 h-14" src="data:image/webp;base64," alt="Workflow" />
                            </a>
                            <div class="hidden md:block">
                                <div class="flex items-baseline ml-10 space-x-4">
                                    <router-link
                                        class="text-gray-300  dark:hover:text-white px-3 py-2 rounded-md text-md font-medium"
                                        to="/gerenciamento">
                                        Solicitados
                                    </router-link>
                                    <router-link
                                        class="text-gray-300  dark:text-white  hover:text-gray-800 dark:hover:text-white px-3 py-2 rounded-md text-md font-medium"
                                        to="/Rejeitados">
                                        Recusados
                                    </router-link>
                                    <router-link
                                        class="text-gray-300  hover:text-gray-800 dark:hover:text-white px-3 py-2 rounded-md text-md font-medium"
                                        to="/Pendentes">
                                        Pendentes
                                    </router-link>

                                    <router-link
                                        class="text-gray-300  hover:text-gray-800 dark:hover:text-white px-3 py-2 rounded-md text-md font-medium"
                                        to="/Concluidos">
                                        Concluidos
                                    </router-link>

                                </div>
                            </div>
                        </div>
                        <div class="block">
                            <div class="flex -mr-2 md:block">
                                <form
                                    class="flex flex-col justify-center w-3/4 max-w-sm space-y-3 md:flex-row md:w-full md:space-x-3 md:space-y-0">
                                    <div class=" relative ">
                                        <input type="text" id="&quot;form-subscribe-Search"
                                            v-model="CPF"
                                            class=" rounded-lg border-transparent flex-1 appearance-none border border-gray-300 w-full py-2 px-4 bg-white text-gray-700 placeholder-gray-400 shadow-sm text-base focus:outline-none focus:ring-2 focus:ring-purple-600 focus:border-transparent"
                                            placeholder="Pesquise por CPF" />
                                    </div>
                                    <button
                                    @click.prevent="getByCPF()"
                                        class="flex-shrink-0 px-4 py-2 text-base font-semibold text-white bg-purple-600 rounded-lg shadow-md hover:bg-purple-700 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:ring-offset-2 focus:ring-offset-purple-200"
                                        type="submit">
                                        Search
                                    </button>
                                </form>
                            </div>
                            <div class="flex items-center ml-4 md:ml-6">
                            </div>
                        </div>
                        <div class="flex -mr-2 md:hidden">
                            <button
